$font-base: 10px;
$width-desktop: 1440px;
$width-tablet: 768px;
$width-mobile: 350px;

@function size($size-in-px) {
  @return $size-in-px / $font-base * 1rem;
}

@function size-desktop($size-in-px) {
  @return $size-in-px / $width-desktop * 100vw;
}

@function size-tablet($size-in-px) {
  @return $size-in-px / $width-tablet * 100vw;
}

@function size-mobile($size-in-px) {
  @return $size-in-px / $width-mobile * 100vw;
}
