body {
  font-family: "Montserrat", "Arial", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol {
  margin: 0;
  padding: 0;
}

main {
  counter-reset: section;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  clip: rect(0 0 0 0);
  overflow: hidden;
}

.block-container {
  &__title {
    position: relative;
    font-size: size-desktop(47px);
    line-height: 1.1em;
    text-transform: uppercase;
    font-weight: 700;

    @media (max-width: 540px) {
      font-size: size-mobile(26px);
    }

    &::before {
      content: "/ 0" counter(section) ".";
      position: absolute;
      bottom: calc(100% + 6px);
      left: 0;
      font-size: size-desktop(24px);
      line-height: 1em;
      font-weight: 600;
      letter-spacing: 0.04em;
      counter-increment: section;

      @media (max-width: 540px) {
        bottom: calc(100% + 2px);
        font-size: size-mobile(11px);
      }
    }
  }
}
