.our-ideas {
    $root: &;

    position: relative;
    color: #ffffff;
    background-color: #184382;
    background-image: linear-gradient(to bottom, #6b1b6a size-desktop(500px), #184382 size-desktop(500px));

    @media (max-width: 540px) {
        padding-top: size-mobile(700px);
        background-image: linear-gradient(to bottom, #6b1b6a size-mobile(140px), #184382 size-mobile(140px));
    }

    &::before {
        content: "";
        position: absolute;
        z-index: 3;
        bottom: 0;
        left: 0;
        width: size-desktop(515px);
        height: size-desktop(505px);
        background-image: url("../img/our-ideas-plant-4.svg");
        background-repeat: no-repeat;
        background-size: contain;

        @media (max-width: 540px) {
            content: none;
        }
    }

    &__top-container {
        position: relative;
        padding-top: size-desktop(146px);

        @media (max-width: 540px) {
            position: absolute;
            padding-top: 0;
            top: size-mobile(24px);
            left: 0;
        }
    }

    &__first-wave, &__second-wave, &__third-wave, &__third-plant {
        background-repeat: no-repeat;
    }

    &__first-thesis, &__second-thesis, &__third-thesis, &__centered-title, &__item-title, &__item-paragraph {
        margin: 0;
    }

    &__first-wave {
        box-sizing: border-box;
        position: relative;
        width: size-desktop(815px);
        height: size-desktop(760px);
        padding-top: size-desktop(128px);
        padding-left: size-desktop(56px);
        color: #000000;
        background-image: url("../img/our-ideas-wave-1.svg");
        background-size: size-desktop(910px) auto;
        background-position: top right;

        @media (max-width: 540px) {
            width: 100%;
            height: size-mobile(300px);
            padding: size-mobile(80px) size-mobile(22px) 0 size-mobile(22px);
            background-image: url("../img/our-ideas-wave-1-mobile.svg");
            background-size: size-mobile(410px) auto;
            background-position: top center;
        }
    }

    &__title {
        margin-bottom: size-desktop(32px);
        max-width: size-desktop(515px);

        @media (max-width: 540px) {
            max-width: 100%;
        }
    }

    &__first-thesis {
        max-width: size-desktop(440px);
        font-size: size-desktop(22px);
        line-height: 1.8em;
        font-weight: 600;

        @media (max-width: 540px) {
            position: relative;
            max-width: 100%;
            font-size: size-mobile(14px);
            line-height: 1.6em;
            font-weight: 500;
            letter-spacing: 0.03em;
        }
    }

    &__second-wave {
        box-sizing: border-box;
        display: flex;
        justify-content: flex-end;
        position: absolute;
        top: size-desktop(264px);
        right: 0;
        width: size-desktop(810px);
        height: size-desktop(660px);
        padding-top: size-desktop(230px);
        padding-right: size-desktop(118px);
        background-image: url("../img/our-ideas-wave-2.svg"), url("../img/our-ideas-wave-2-faded.svg");
        background-size: size-desktop(910px) auto;
        background-position: top left, top size-desktop(33px) left size-desktop(10px);

        @media (max-width: 540px) {
            position: relative;
            top: size-mobile(-92px);
            width: 100%;
            height: size-mobile(280px);
            padding: size-mobile(56px) size-mobile(22px) 0 size-mobile(10px);
            background-image: url("../img/our-ideas-wave-2-mobile.svg");
            background-size: size-mobile(400px) auto;
            background-position: top left size-desktop(10px);
        }
    }

    &__first-plant {
        position: absolute;
        top: size-desktop(-114px);
        right: 0;
        width: size-desktop(317px);
        height: size-desktop(346px);

        @media (max-width: 540px) {
            display: none;
        }

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transform: rotate(180deg);
            background-image: url("../img/our-ideas-plant-1.svg");
        }
    }

    &__second-thesis {
        max-width: size-desktop(600px);
        font-size: size-desktop(26px);
        line-height: 1.5em;
        letter-spacing: 0.02em;
        color: #000000;
        text-align: right;

        @media (max-width: 540px) {
            max-width: 100%;
            font-size: size-mobile(14px);
            line-height: 1.6em;
            letter-spacing: -0.02em;
        }

        strong {
            font-weight: 700;
        }
    }

    &__third-wave {
        position: absolute;
        top: size-desktop(550px);
        padding-left: size-desktop(128px);
        padding-top: size-desktop(96px);
        color: #000000;

        @media (max-width: 540px) {
            box-sizing: border-box;
            position: relative;
            top: size-mobile(-170px);
            height: size-mobile(250px);
            padding-left: size-mobile(22px);
            padding-top: size-mobile(70px);
            background-image: url("../img/our-ideas-wave-3.svg"), url("../img/our-ideas-wave-3-faded.svg");
            background-size: 335px auto;
            background-position: top left size-mobile(-50px),
                top size-mobile(16px) left size-mobile(-40px);
        }
    }

    &__third-thesis {
        max-width: size-desktop(540px);
        font-size: size-desktop(27px);
        line-height: 1.6em;
        font-weight: 700;
        letter-spacing: -0.05em;

        @media (max-width: 540px) {
            max-width: size-mobile(220px);
            font-size: size-mobile(16px);
            line-height: 1.4em;
            letter-spacing: 0.03em;
        }
    }

    &__second-plant {
        position: absolute;
        top: 0;
        left: 0;
        width: size-desktop(160px);
        height: size-desktop(520px);

        @media (max-width: 540px) {
            right: 0;
            left: auto;
            width: size-mobile(95px);
            height: 100%;
        }

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transform: scaleX(-1);
            background-image: url("../img/our-ideas-stones-1.svg"), url("../img/our-ideas-plant-2.svg");
            background-position: bottom size-desktop(72px) left, top left size-desktop(40px);
            background-repeat: no-repeat;

            @media (max-width: 540px) {
                transform: none;
                background-position: bottom left, top left size-mobile(4px);
            }
        }
    }

    &__centered-title {
        max-width: size-desktop(580px);
        margin: 0 auto;
        margin-top: size-desktop(48px);
        font-size: size-desktop(31px);
        line-height: 1.4em;
        text-align: center;

        @media (max-width: 540px) {
            max-width: 100%;
            margin-top: 0;
            margin-bottom: size-mobile(24px);
            font-size: size-mobile(16px);
            line-height: 1.3em;
        }
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;
        counter-reset: list;
    }

    &__item-wrapper {
        position: relative;
        counter-increment: list;

        &::before, &::after {
            content: "";
            position: absolute;
            background-repeat: no-repeat;
        }

        &::before {
            top: 0;
            left: 0;
            width: 100%;
        }

        &--proteins-and-carbohydrates {
            padding-left: size-desktop(128px);
            padding-top: size-desktop(128px);
            color: #000000;

            @media (max-width: 540px) {
                text-align: right;
                padding: size-mobile(80px) size-mobile(22px) 0 size-mobile(22px);
            }

            #{$root}__item-description {
                max-width: size-desktop(1120px);

                @media (max-width: 540px) {
                    max-width: 100%;
                }
            }

            &::before {
                height: size-desktop(586px);
                background-image: url("../img/our-ideas-wave-4.svg");
                background-size: cover;
                background-position: left size-desktop(-60px) top;

                @media (max-width: 540px) {
                    height: size-mobile(450px);
                    background-image: url("../img/our-ideas-wave-4-mobile.svg");
                    background-size: size-mobile(440px) auto;
                    background-position: top center;
                }
            }

            &::after {
                top: size-desktop(-72px);
                right: size-desktop(100px);
                width: size-desktop(252px);
                height: size-desktop(260px);
                transform: rotate(240deg) scaleY(-1);
                background-image: url("../img/our-ideas-figure-1.svg");
                background-size: contain;

                @media (max-width: 540px) {
                    top: size-mobile(-14px);
                    right: auto;
                    left: size-mobile(6px);
                    width: size-mobile(122px);
                    height: size-mobile(126px);
                    transform: scaleX(-1);
                }
            }
        }

        &--lipids {
            padding-top: size-desktop(160px);
            padding-right: size-desktop(78px);
            text-align: right;
            display: flex;
            flex-direction: column;
            align-items: flex-end;

            @media (max-width: 540px) {
                align-items: flex-start;
                padding: size-mobile(64px) 0 0 size-mobile(22px);
                margin-top: size-mobile(14px);
                text-align: left;
            }

            #{$root}__item-description {
                max-width: size-desktop(730px);

                @media (max-width: 540px) {
                    max-width: size-mobile(240px);
                }
            }

            #{$root}__item-paragraph:last-child {
                @media (max-width: 540px) {
                    max-width: size-mobile(180px);
                }
            }

            &::before {
                top: size-desktop(-10px);
                height: size-desktop(736px);
                background-image: url("../img/our-ideas-wave-5.svg"),
                    url("../img/our-ideas-wave-5-faded.svg");
                background-position: right size-desktop(-120px) top,
                    right size-desktop(-105px) top size-desktop(24px);
                background-size: size-desktop(1290px) auto;

                @media (max-width: 540px) {
                    top: 0;
                    height: size-mobile(460px);
                    background-image: url("../img/our-ideas-wave-5-mobile.svg"),
                        url("../img/our-ideas-wave-5-mobile-faded.svg");
                    background-position: left size-mobile(-160px) top,
                        left size-mobile(-142px) top size-mobile(12px);
                    background-size: size-mobile(515px) auto;
                }
            }

            &::after {
                top: size-desktop(234px);
                left: size-desktop(400px);
                width: size-desktop(330px);
                height: size-desktop(330px);
                transform: scaleX(-1);
                background-image: url("../img/our-ideas-figure-2.svg"),
                    url("../img/our-ideas-figure-2-bg.svg");
                background-size: size-desktop(210px) auto, 100% auto;
                background-position: right size-desktop(30px) top, left bottom;

                @media (max-width: 540px) {
                    top: auto;
                    right: size-mobile(56px);
                    bottom: size-mobile(24px);
                    left: auto;
                    width: size-mobile(100px);
                    height: size-mobile(136px);
                    background-image: url("../img/our-ideas-figure-2.svg");
                    background-size: contain;
                    background-position: top center;
                }
            }
        }

        &--biosynthesis {
            margin-top: size-desktop(232px);
            padding-left: size-desktop(156px);

            @media (max-width: 540px) {
                text-align: right;
                margin-top: size-mobile(60px);
                padding: 0 size-mobile(22px);
            }

            #{$root}__item-title {
                max-width: size-desktop(500px);

                @media (max-width: 540px) {
                    max-width: 100%;
                }
            }

            #{$root}__item-description {
                max-width: size-desktop(920px);

                @media (max-width: 540px) {
                    max-width: 100%;
                }
            }

            #{$root}__item-paragraph:last-child {
                max-width: size-desktop(800px);

                @media (max-width: 540px) {
                    max-width: 100%;
                }
            }

            &::after {
                z-index: 3;
                top: size-desktop(48px);
                right: size-desktop(120px);
                width: size-desktop(205px);
                height: size-desktop(320px);
                transform: scaleX(-1);
                background-image: url("../img/our-ideas-figure-3.svg");

                @media (max-width: 540px) {
                    content: none;
                }
            }
        }

        &--green-shelf {
            padding-top: size-desktop(104px);
            padding-right: size-desktop(78px);
            padding-bottom: size-desktop(148px);
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            color: #000000;
            text-align: right;

            @media (max-width: 540px) {
                align-items: flex-start;
                padding: size-mobile(66px) size-mobile(22px) size-mobile(140px) size-mobile(22px);
                text-align: left;
            }

            #{$root}__item-description {
                max-width: size-desktop(660px);

                @media (max-width: 540px) {
                    max-width: size-mobile(240px);
                    font-size: size-mobile(14px);
                    letter-spacing: 0.03em;
                }
            }

            &::before {
                top: size-desktop(-136px);
                height: size-desktop(700px);
                background-image: url("../img/our-ideas-wave-6.svg");
                background-size: size-desktop(1047px) auto;
                background-position: top right;

                @media (max-width: 540px) {
                    top: 0;
                    height: size-mobile(460px);
                    background-image: url("../img/our-ideas-wave-6-mobile.svg"),
                        url("../img/our-ideas-wave-6-mobile-faded.svg");
                    background-size: size-mobile(508px) auto;
                    background-position: top right size-mobile(-30px),
                        top size-mobile(24px) right size-mobile(-46px);
                }
            }

            &::after {
                top: size-desktop(180px);
                left: size-desktop(500px);
                width: size-desktop(250px);
                height: size-desktop(300px);
                transform: scaleX(-1);
                background-image: url("../img/our-ideas-figure-4.svg");
                background-size: contain;

                @media (max-width: 540px) {
                    top: size-mobile(96px);
                    right: size-mobile(22px);
                    left: auto;
                    width: size-mobile(116px);
                    height: size-mobile(136px);
                    transform: none;
                }
            }
        }
    }

    &__biosynthesis-image {
        display: none;
        width: size-mobile(90px);
        height: auto;
        padding: size-mobile(10px);
        float: left;

        @media (max-width: 540px) {
            display: block;
        }
    }

    &__item-title, &__item-description {
        position: relative;
        z-index: 2;
    }

    &__item-title {
        font-size: size-desktop(27px);
        line-height: 1.6em;
        letter-spacing: -0.03em;
        font-weight: 700;

        @media (max-width: 540px) {
            font-size: size-mobile(13px);
            letter-spacing: 0.01em;
        }

        &::before {
            content: counter(list) ". ";
        }
    }

    &__item-description {
        margin-top: size-desktop(16px);
        font-size: size-desktop(24px);
        line-height: 1.5em;
        letter-spacing: 0.02em;
        font-weight: 500;

        @media (max-width: 540px) {
            margin-top: size-mobile(10px);
            font-size: size-mobile(13px);
            line-height: 1.6em;
            letter-spacing: 0.01em;
        }
    }

    &__item-paragraph {
        margin-bottom: size-desktop(8px);

        @media (max-width: 540px) {
            margin-bottom: 0;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__third-plant {
        position: absolute;
        top: size-desktop(104px);
        left: 0;
        width: size-desktop(328px);
        height: size-desktop(580px);
        background-image: url("../img/our-ideas-plant-3.svg");
        background-size: contain;

        @media (max-width: 540px) {
            display: none;
        }
    }
}
