.footer {
  $root: &;

  position: relative;
  z-index: 2;
  display: flex;
  background-image: url("../img/footer-bg.svg");
  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: auto 90%;
  background-color: #ffffff;

  @media (max-width: 768px) {
    flex-direction: column;

    background-image: url("../img/footer-bg.svg"),
      linear-gradient(to bottom, transparent 96px, #d4d4d4 96px, #d4d4d4 97px, transparent 97px);
    background-position: bottom right, 0 0;
    background-size: auto 90%, 100%;
  }

  &__logo-link {
    display: block;
    width: 470px;
    padding: 70px 0;
    border-right: 1px solid #d4d4d4;

    @media (max-width: 768px) {
      width: 100%;
      padding: 12px 0;
      border-right: none;
    }

    &:hover {
      #{$root}__logo-image {
        opacity: 0.8;
      }
    }

    &:active {
      #{$root}__logo-image {
        opacity: 0.3;
      }
    }
  }

  &__logo-image {
    display: block;
    height: auto;
    margin: 0 auto;
    transition: opacity $transition-duration;

    @media (max-width: 768px) {
      width: 85px;
    }
  }

  &__links-wrapper {
    padding-left: 118px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    @media (max-width: 768px) {
      padding: 26px 42px 32px 42px;
      display: block;
    }
  }

  &__email-link {
    position: relative;
    display: inline-block;
    margin-bottom: 28px;
    font-size: 22px;
    line-height: 1.3em;
    text-transform: uppercase;
    text-decoration: none;
    color: #282828;

    @media (max-width: 768px) {
      margin-bottom: 16px;
      font-size: 10px;
    }

    &::after {
      content: "";
      position: absolute;
      top: calc(100% + 2px);
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #282828;
    }
  }

  &__email-link,
  &__social-link {
    transition: opacity $transition-duration;

    &:hover {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.3;
    }
  }

  &__social-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }

  &__social-item {
    margin-right: 36px;

    @media (max-width: 768px) {
      margin-right: 24px;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__social-link {
    display: block;
    width: 60px;
    height: 50px;
    background-repeat: no-repeat;
    background-position: center left;

    @media (max-width: 768px) {
      width: 28px;
      height: 23px;
    }

    &--fb {
      background-image: url("../img/facebook-icon.svg");
    }

    &--vk {
      background-image: url("../img/vk-icon.svg");
    }

    &--tg {
      background-image: url("../img/telegram-icon.svg");
    }

    &--ig {
      background-image: url("../img/instagram-icon-thin.svg");
    }
  }
}
