.inevitability-block {
    $root: &;

    position: relative;
    padding-top: size-desktop(195px);
    padding-bottom: size-desktop(140px);
    padding-left: size-desktop(56px);
    padding-right: size-desktop(40px);
    background: url("../img/inevitability-bg-1@1x.png") top left no-repeat,
        url("../img/inevitability-bg-2@1x.png") top right no-repeat,
        url("../img/inevitability-population.svg") top size-desktop(270px) left size-desktop(-80px)
        no-repeat,
        url("../img/inevitability-population-bg.svg") top size-desktop(270px) left size-desktop(-50px)
        no-repeat,
        url("../img/inevitability-problems.svg") top size-desktop(600px) right size-desktop(-210px)
        no-repeat,
        url("../img/inevitability-join-us.svg") top size-desktop(870px) right size-desktop(-135px)
        no-repeat,
        url("../img/inevitability-technologies.svg") top size-desktop(1050px) left size-desktop(-10px)
        no-repeat,
        url("../img/inevitability-technologies-bg.svg") top size-desktop(1050px) left size-desktop(30px)
        no-repeat,
        url("../img/block-bottom@1x.png") bottom size-desktop(40px) left 0 no-repeat,
        linear-gradient(to bottom, #ffb700 37%, #e4e3e8 37%);
    background-size: 34.9% auto,
        54.56% auto,
        size-desktop(1070px) auto,
        size-desktop(1070px) auto,
        size-desktop(1060px) auto,
        size-desktop(680px) auto,
        size-desktop(780px) auto,
        size-desktop(780px) auto,
        size-desktop(1440px),
        auto,
        auto;
    z-index: 0;
    overflow: hidden;

    @media (min-width: 1440px) and (min-resolution: 1.5dppx),
        (min-width: 1440px) and (min-resolution: 144dpi) {
        background: url("../img/inevitability-bg-1@2x.png") top left no-repeat,
            url("../img/inevitability-bg-2@2x.png") top right no-repeat,
            url("../img/inevitability-population.svg") top size-desktop(270px) left size-desktop(-80px)
            no-repeat,
            url("../img/inevitability-population-bg.svg") top size-desktop(270px) left size-desktop(-50px)
            no-repeat,
            url("../img/inevitability-problems.svg") top size-desktop(600px) right size-desktop(-210px)
            no-repeat,
            url("../img/inevitability-join-us.svg") top size-desktop(870px) right size-desktop(-135px)
            no-repeat,
            url("../img/inevitability-technologies.svg") top size-desktop(1050px) left size-desktop(-10px)
            no-repeat,
            url("../img/inevitability-technologies-bg.svg") top size-desktop(1050px) left
            size-desktop(30px) no-repeat,
            url("../img/block-bottom@2x.png") bottom size-desktop(40px) left 0 no-repeat,
            linear-gradient(to bottom, #ffb700 37%, #e4e3e8 37%);
        background-size: 34.9% auto,
            54.56% auto,
            size-desktop(1070px) auto,
            size-desktop(1070px) auto,
            size-desktop(1060px) auto,
            size-desktop(680px) auto,
            size-desktop(780px) auto,
            size-desktop(780px) auto,
            size-desktop(1440px) auto,
            auto;
    }

    @media (max-width: 540px) and (min-resolution: 1.5dppx),
        (max-width: 540px) and (min-resolution: 144dpi) {
        padding-top: size-mobile(130px);
        padding-bottom: size-mobile(70px);
        padding-left: size-mobile(20px);
        padding-right: size-mobile(20px);
        background: url("../img/inevitability-top-bg@2x.png") top no-repeat,
            url("../img/inevitability-population-mobile.svg") top size-mobile(170px) left
            size-mobile(-70px) no-repeat,
            url("../img/inevitability-population-mobile-bg.svg") top size-mobile(164px) left
            size-mobile(-50px) no-repeat,
            url("../img/inevitability-club-mobile.svg") top size-mobile(906px) left size-mobile(-88px)
            no-repeat,
            url("../img/inevitability-club-mobile-bg.svg") top size-mobile(900px) left size-mobile(-72px)
            no-repeat,
            url("../img/inevitability-problems-mobile.svg") top size-mobile(520px) right
            size-mobile(-70px) no-repeat,
            url("../img/inevitability-technologies-mobile.svg") top size-mobile(1220px) right
            size-mobile(-70px) no-repeat,
            url("../img/inevitability-technologies-mobile-bg.svg") top size-mobile(1240px) right
            size-mobile(-80px) no-repeat,
            url("../img/block-bottom-mobile@2x.png") bottom size-mobile(20px) left 0 no-repeat,
            url("../img/inevitability-bottom-bg@2x.png") bottom no-repeat,
            linear-gradient(to bottom, #ffb700 30%, #e4e3e8 30%);
        background-size: 100% auto,
            size-mobile(470px) auto,
            size-mobile(470px) auto,
            size-mobile(400px) auto,
            size-mobile(410px) auto,
            size-mobile(410px) auto,
            size-mobile(400px) auto,
            size-mobile(400px) auto,
            size-mobile(350px) auto,
            95% auto,
            auto;
    }

    @media (max-width: 540px) {
        padding-top: size-mobile(130px);
        padding-bottom: size-mobile(70px);
        padding-left: size-mobile(20px);
        padding-right: size-mobile(20px);
        background: url("../img/inevitability-top-bg@1x.png") top no-repeat,
            url("../img/inevitability-population-mobile.svg") top size-mobile(170px) left
            size-mobile(-70px) no-repeat,
            url("../img/inevitability-population-mobile-bg.svg") top size-mobile(164px) left
            size-mobile(-50px) no-repeat,
            url("../img/inevitability-club-mobile.svg") top size-mobile(906px) left size-mobile(-88px)
            no-repeat,
            url("../img/inevitability-club-mobile-bg.svg") top size-mobile(900px) left size-mobile(-72px)
            no-repeat,
            url("../img/inevitability-problems-mobile.svg") top size-mobile(520px) right
            size-mobile(-70px) no-repeat,
            url("../img/inevitability-technologies-mobile.svg") top size-mobile(1220px) right
            size-mobile(-70px) no-repeat,
            url("../img/inevitability-technologies-mobile-bg.svg") top size-mobile(1240px) right
            size-mobile(-80px) no-repeat,
            url("../img/block-bottom-mobile@1x.png") bottom size-mobile(20px) left 0 no-repeat,
            url("../img/inevitability-bottom-bg@1x.png") bottom no-repeat,
            linear-gradient(to bottom, #ffb700 30%, #e4e3e8 30%);
        background-size: 100% auto,
            size-mobile(470px) auto,
            size-mobile(470px) auto,
            size-mobile(400px) auto,
            size-mobile(410px) auto,
            size-mobile(410px) auto,
            size-mobile(400px) auto,
            size-mobile(400px) auto,
            size-mobile(350px) auto,
            95% auto,
            auto;
    }

    &__title {
        margin-bottom: size-desktop(200px);

        @media (max-width: 540px) {
            margin-bottom: size-mobile(75px);
        }
    }

    &__subtitle {
        margin-bottom: size-desktop(32px);
        line-height: 1.3em;
        font-size: size-desktop(28px);
        letter-spacing: 0.03em;

        @media (max-width: 540px) {
            margin-bottom: size-mobile(15px);
            font-size: size-mobile(16px);
        }
    }

    &__founder-image {
        position: absolute;
        top: size-desktop(90px);
        right: size-desktop(170px);
        width: size-desktop(397px);
        height: auto;

        @media (max-width: 540px) {
            top: size-mobile(542px);
            right: size-mobile(-10px);
            width: size-mobile(210px);
            overflow-x: hidden;
        }
    }

    &__founder-caption {
        position: absolute;
        top: size-desktop(450px);
        right: size-desktop(56px);
        font-weight: 600;
        text-align: right;

        @media (max-width: 540px) {
            display: none;
        }
    }

    &__scientist {
        position: absolute;
        top: size-desktop(770px);
        left: size-desktop(105px);
        width: size-desktop(440px);
        height: size-desktop(470px);
        background: url("../img/inevitability-scientist.svg") no-repeat,
            url("../img/inevitability-scientist-bg@1x.png") top size-desktop(20px) left size-desktop(20px)
            no-repeat;
        background-size: size-desktop(400px) auto;

        @media (min-width: 541px) and (min-resolution: 1.5dppx),
            (min-width: 541px) and (min-resolution: 144dpi) {
            background: url("../img/inevitability-scientist.svg") no-repeat,
                url("../img/inevitability-scientist-bg@2x.png") top size-desktop(20px) left
                size-desktop(20px) no-repeat;
            background-size: size-desktop(400px) auto;
        }

        @media (max-width: 540px) and (min-resolution: 1.5dppx),
            (max-width: 540px) and (min-resolution: 144dpi) {
            background: url("../img/inevitability-scientist.svg") top size-mobile(8px) left
                size-mobile(8px) no-repeat,
                url("../img/inevitability-scientist-bg@2x.png") no-repeat;
            background-size: size-mobile(190px) auto;
            top: size-mobile(1100px);
            left: 0;
            width: size-mobile(200px);
            height: size-mobile(230px);
        }

        @media (max-width: 540px) {
            background: url("../img/inevitability-scientist.svg") top size-mobile(8px) left
                size-mobile(8px) no-repeat,
                url("../img/inevitability-scientist-bg@1x.png") no-repeat;
            background-size: size-mobile(190px) auto;
            top: size-mobile(1100px);
            left: 0;
            width: size-mobile(200px);
            height: size-mobile(230px);
        }
    }

    &__ellips {
        position: absolute;
        top: size-desktop(1220px);
        right: size-desktop(-130px);
        width: size-desktop(260px);
        height: size-desktop(260px);
        background-image: url("../img/inevitability-ellips.svg");
        background-repeat: no-repeat;
        background-size: contain;

        @media (max-width: 768px) {
            display: none;
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    &__item {
        font-weight: 500;
        line-height: 1.5em;

        &_population {
            margin-bottom: size-desktop(96px);
            margin-right: auto;
            max-width: size-desktop(840px);
            font-size: size-desktop(22px);
            letter-spacing: 0.03em;

            @media (max-width: 540px) {
                margin-bottom: size-mobile(210px);
                max-width: size-mobile(280px);
                line-height: 1.6em;
                font-size: size-mobile(14px);
                letter-spacing: 0.02em;
            }
        }

        &_problems {
            margin-left: auto;
            margin-bottom: size-desktop(50px);
            max-width: size-desktop(720px);
            font-size: size-desktop(26px);
            color: #ffffff;
            text-align: right;
            text-indent: size-desktop(80px);
            letter-spacing: -0.045em;

            @media (max-width: 540px) {
                margin-bottom: size-mobile(80px);
                max-width: size-mobile(300px);
                text-indent: 0;
                letter-spacing: 0.01em;
            }
        }

        &_club {
            margin-bottom: size-desktop(100px);
            margin-left: auto;
            max-width: size-desktop(690px);
            font-size: size-desktop(26px);
            color: #ffffff;
            text-align: right;
            letter-spacing: -0.045em;

            @media (max-width: 540px) {
                margin-bottom: size-mobile(214px);
                margin-right: auto;
                margin-left: 0;
                max-width: size-mobile(280px);
                color: inherit;
                text-align: left;
                font-weight: 600;
                letter-spacing: 0.04em;
            }
        }

        &_technologies {
            margin-right: auto;
            margin-bottom: size-desktop(24px);
            max-width: size-desktop(680px);
            font-size: size-desktop(24px);
            letter-spacing: 0.04em;

            @media (max-width: 540px) {
                margin-bottom: size-mobile(72px);
                margin-right: 0;
                margin-left: auto;
                max-width: size-mobile(280px);
                text-align: right;
                letter-spacing: 0.01em;
            }
        }

        &_join-us {
            margin-left: auto;
            width: size-desktop(726px);
            font-size: size-desktop(30px);
            font-weight: 700;
            text-align: right;
            text-indent: size-desktop(64px);

            @media (max-width: 540px) {
                margin-top: 0;
                margin-right: auto;
                width: 100%;
                text-align: justify;
                text-indent: 0;
            }

            #{$root}__founder {
                font-weight: 600;
            }
        }

        @media (max-width: 540px) {
            font-size: size-mobile(14px);
        }
    }

    &__founder-name {
        margin: 0;
        margin-top: size-desktop(35px);
        margin-bottom: size-desktop(8px);
        line-height: 1.1em;
        font-size: size-desktop(22px);
        font-weight: 700;
        text-align: right;

        @media (max-width: 540px) {
            margin-top: size-mobile(30px);
            margin-bottom: size-mobile(12px);
            font-size: size-mobile(14px);
        }
    }

    &__founder {
        margin: 0;
        line-height: 1.1em;
        font-size: size-desktop(19px);
        text-align: right;

        @media (max-width: 540px) {
            font-size: size-mobile(10px);
        }
    }
}

