            url("../img/why-plants-leaf@1x.png") top 0 right 20px no-repeat,
            url("../img/why-plants-cow@1x.png") bottom 350px left 0 no-repeat,
            url("../img/inevitability-bottom@1x.png") bottom 90px left 0 no-repeat,
