    @media (max-width: 900px) and (min-resolution: 1.5dppx),
        (max-width: 900px) and (min-resolution: 144dpi) {
        padding-bottom: 200px;
        padding-left: 2%;
        padding-right: 2%;
        background: url("../img/why-plants-leaf@2x.png") bottom 310px left -30px no-repeat,
            url("../img/why-plants-leaf@2x.png") top 0 right 20px no-repeat,
            url("../img/why-plants-cow@2x.png") bottom 350px left 0 no-repeat,
            url("../img/inevitability-bottom@2x.png") bottom 90px left 0 no-repeat,
            url("../img/why-plants-culture@2x.png") top 1300px right -200px no-repeat,
