            no-repeat,
            url("../img/inevitability-bottom@1x.png") bottom 50px left 0 no-repeat,
            url("../img/why-plants-progress-mobile@1x.png") top 640px right -50px no-repeat,
            url("../img/why-plants-culture-mobile@1x.png") top 1270px right -100px no-repeat,
            url("../img/why-plants-health-mobile@1x.png") top 870px left -50px no-repeat,
            url("../img/why-plants-history-mobile@1x.png") top 100px left -170px no-repeat,
            url("../img/why-plants-bottom-bg@1x.png") bottom -50px right -100px
            no-repeat,
            #e4e3e9;
        background-size: 300px auto,
            100% auto,
            600px auto,
            640px auto,
            577px auto,
            800px auto,
            239px auto,
            auto;
    }

    @media (max-width: 768px) and (min-resolution: 1.5dppx),
        (max-width: 768px) and (min-resolution: 144dpi) {
        padding-bottom: 120px;
        padding-left: 3%;
        padding-right: 3%;
        background: url("../img/why-plants-cow-mobile@2x.png") top 1080px left 300px
            no-repeat,
            url("../img/inevitability-bottom@2x.png") bottom 50px left 0 no-repeat,
            url("../img/why-plants-progress-mobile@2x.png") top 640px right -50px no-repeat,
            url("../img/why-plants-culture-mobile@2x.png") top 1270px right -100px no-repeat,
            url("../img/why-plants-health-mobile@2x.png") top 870px left -50px no-repeat,
