        (max-width: 540px) and (min-resolution: 144dpi) {
        padding-bottom: 120px;
        padding-left: 3%;
        padding-right: 3%;
        background: url("../img/why-plants-cow-mobile@2x.png") top 1080px left 200px
            no-repeat,
            url("../img/inevitability-bottom@2x.png") bottom 50px left 0 no-repeat,
            url("../img/why-plants-progress-mobile@2x.png") top 640px right -50px no-repeat,
            url("../img/why-plants-culture-mobile@2x.png") top 1300px right -100px no-repeat,
            url("../img/why-plants-health-mobile@2x.png") top 950px left -50px no-repeat,
            url("../img/why-plants-history-mobile@2x.png") top 245px left -160px no-repeat,
