    &__circle {
        position: absolute;
        top: 1000px;
        right: 100px;
        width: 206px;
        height: 206px;
        background-image: url("../img/why-plants-circle@1x.png");

        @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
            background-image: url("../img/why-plants-circle@2x.png");
        }

        @media (max-width: 1400px) {
            top: 1600px;
        }

        @media (max-width: 1200px) {
            top: 1250px;
        }

        @media (max-width: 900px) {
            display: none;
        }
    }

    &__title {
        padding-top: 200px;
        margin-bottom: 125px;

        @media (max-width: 768px) {
            margin-bottom: 90px;

            &::before {
                bottom: 45px;
            }
        }

        @media (max-width: 540px) {
            margin-bottom: 20px;
            width: 250px;

            &::before {
                bottom: 110px;
            }
        }
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    &__item {
        line-height: 40px;

        @media (max-width: 768px) {
            line-height: 20px;
            font-size: 18px;
        }

        @media (max-width: 540px) {
            line-height: 16px;
            font-size: 14px;
        }

        &--history {
            width: 720px;
            font-size: 22px;
            font-weight: 600;

            @media (max-width: 1200px) {
                line-height: 22px;
                font-size: 20px;
            }

            @media (max-width: 768px) {
                margin-bottom: 170px;
                width: 400px;
            }

            @media (max-width: 540px) {
                margin-bottom: 120px;
                width: 260px;
            }
        }

        &--progress {
            margin-left: auto;
            margin-top: -250px;
            margin-bottom: 30px;
            width: 500px;
            font-size: 23px;
            text-align: right;

            @media (max-width: 1400px) {
                margin-top: 120px;
                margin-bottom: 120px;
            }

            @media (max-width: 1200px) {
                margin-top: 230px;
                margin-bottom: 170px;
                line-height: 22px;
                font-size: 21px;
            }

            @media (max-width: 768px) {
                margin-top: 0;
                margin-bottom: 110px;
                width: 470px;
            }

            @media (max-width: 540px) {
                margin-top: 0;
                margin-bottom: 110px;
                width: 270px;
            }
        }

        &--health {
            margin-bottom: 90px;
            width: 785px;
            font-size: 24px;

            @media (max-width: 1400px) {
                margin-bottom: 320px;
            }

            @media (max-width: 1200px) {
                margin-bottom: 260px;
                width: 700px;
                line-height: 24px;
                font-size: 22px;
            }

            @media (max-width: 768px) {
                margin-bottom: 180px;
                width: 300px;
            }

            @media (max-width: 540px) {
                margin-bottom: 150px;
                width: 200px;
            }
        }

        &--culture {
            margin-bottom: 100px;
            margin-left: auto;
            width: 600px;
            font-size: 23px;
            text-align: right;

            @media (max-width: 1200px) {
                margin-bottom: 80px;
            }

            @media (max-width: 768px) {
                margin-bottom: 170px;
                width: 400px;
            }

            @media (max-width: 540px) {
                margin-bottom: 130px;
                width: 268px;
            }
        }

        &--answer {
            margin-left: auto;
            width: 800px;
            font-size: 24px;
            font-weight: 600;
            text-align: right;

            @media (max-width: 1400px) {
                width: 700px;
            }

            @media (max-width: 1200px) {
                line-height: 22px;
                font-size: 21px;
            }

            @media (max-width: 768px) {
                width: 100%;
                text-align: justify;
            }
        }
    }
}
